.coaches {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $color-default-white;
}

.coaches__container {
  max-width: 1366px;
  width: 100%;
  min-height: 104px;
  padding: 0 103px 140px 103px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.coaches h2 {
  font-family: $ff-alumni;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  color: $color-sapphire;
  margin: 0;
  margin-bottom: 48px;
}

.coaches__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.coaches__swiper {
  width: 1160px;
  height: 294px;
}

.coaches__swiper-button-prev,
.coaches__swiper-button-next {
  position: absolute;
  top: 44%;
  width: 27px;
  height: 51px;
  background-color: transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.coaches__swiper-button-prev:focus,
.coaches__swiper-button-next:focus {
  outline: 3px solid $color-egyptian-blue;
  border-radius: 4px;
}

.coaches__swiper-button-prev:hover,
.coaches__swiper-button-next:hover {
  outline: 3px solid $color-egyptian-blue;
  border-radius: 4px;
}

.coaches__swiper-button-prev {
  left: 47px;
}

.coaches__swiper-button-next {
  right: 47px;
}
